import React, { useState } from 'react';
import { getStrategyTitleText } from './tableTitle';
import { Table, Tooltip } from 'antd';
const { Column } = Table;

const getColumn = (item) => {
  if (item == "instance_id") {
    // return <Column title="instance_id" dataIndex="instance_id" key="instance_id" width={50}/>;
    return <Column title={getStrategyTitleText("instance_id")} dataIndex="instance_id" key="instance_id" width={50} />;
  } else if (item == "strategy_id") {
    return <Column title={getStrategyTitleText("strategy_id")} dataIndex="strategy_id" key="strategy_id" width={50} />
  } else if (item == "name") {
    return <Column title={getStrategyTitleText("name")} dataIndex="name" key="name" width={100} />
  } else if (item == "sender") {
    return <Column title={getStrategyTitleText("sender")} dataIndex="sender" key="sender" width={160}
      ellipsis={{ showTitle: false }}
      render={sender => (
        <Tooltip placement="topLeft" title={sender}>
          {sender}
        </Tooltip>
      )}
    />
  } else if (item == "strategy_startup_amount") {
    return <Column title={getStrategyTitleText("strategy_startup_amount")} dataIndex="strategy_startup_amount" key="strategy_startup_amount" />
  } else if (item == "take_profit") {
    return <Column title={getStrategyTitleText("take_profit")} dataIndex="take_profit" key="take_profit" />
  } else if (item == "stop_loss") {
    return <Column title={getStrategyTitleText("stop_loss")} dataIndex="stop_loss" key="stop_loss" />
  } else if (item == "tp_rate") {
    return <Column title={getStrategyTitleText("tp_rate")} dataIndex="tp_rate" key="tp_rate" />
  } else if (item == "sl_rate") {
    return <Column title={getStrategyTitleText("sl_rate")} dataIndex="sl_rate" key="sl_rate" />
  } else if (item == "exchange") {
    return <Column title={getStrategyTitleText("exchange")} dataIndex="exchange" key="exchange" />
  } else if (item == "status") {
    return <Column title={getStrategyTitleText("status")} dataIndex="statusText" key="status" />
  } else if (item == "user_address") {
    return <Column title={getStrategyTitleText("user_address")} dataIndex="user_address" key="user_address" width={160}
      ellipsis={{ showTitle: false }}
      render={sender => (
        <Tooltip placement="topLeft" title={sender}>
          {sender}
        </Tooltip>
      )} />
  } else if (item == "apikey") {
    return <Column title={getStrategyTitleText("apikey")} dataIndex="apikey" key="apikey"
      ellipsis={{ showTitle: false }}
      render={sender => (
        <Tooltip placement="topLeft" title={sender}>
          {sender}
        </Tooltip>
      )} />

  } else if (item == "secretkey") {
    return <Column title={getStrategyTitleText("secretkey")} dataIndex="secretkey" key="secretkey" width={160} ellipsis={true} />
  } else if (item == "passphrase") {
    return <Column title={getStrategyTitleText("passphrase")} dataIndex="passphrase" key="passphrase" />
  } else if (item == "instance_status") {
    return <Column title={getStrategyTitleText("instance_status")} dataIndex="instance_status_text" key="instance_status" />
  }
}

export {
  getColumn
};